.entrenamientoDeFuerza {
  width: 234px;
  position: relative;
  line-height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.heading3 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-74xl-4);
  font-size: var(--font-size-9xl);
}
.icon {
  width: 54px;
  position: relative;
  height: 54px;
}
.container2 {
  width: 55px;
  height: 55px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.container1,
.enElTaller {
  display: flex;
  align-items: center;
}
.container1 {
  align-self: stretch;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 var(--padding-90xl);
}
.enElTaller {
  width: 376px;
  position: relative;
  line-height: 26px;
}
.item,
.list {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.item {
  align-items: center;
  padding: 0 var(--padding-80xl-4) var(--padding-3xs);
}
.list {
  align-items: flex-start;
  text-align: left;
}
.leerMs {
  position: relative;
  line-height: 24px;
  font-weight: 900;
}
.link {
  border-radius: var(--br-31xl);
  background-color: var(--color-goldenrod-100);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-2xs) var(--padding-21xl-8) var(--padding-2xs-8);
  font-family: var(--bootstrapmadecom-nunito-medium-14);
}
.container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: -0.1px;
  right: 0;
  bottom: 0.1px;
  left: 0;
  border-radius: var(--br-9xs);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-21xl) var(--padding-xl);
  box-sizing: border-box;
  gap: var(--gap-mini);
}
.background {
  align-self: stretch;
  position: relative;
  border-radius: var(--br-xl);
  background-color: var(--color-whitesmoke);
  height: 414px;
  text-align: center;
  font-size: var(--bootstrapmadecom-nunito-light-16-size);
  color: var(--color-gray-100);
  font-family: var(--bootstrapmadecom-roboto-bold-13-upper);
}
